import { imagePath } from '@nextcloud/router';
export default {
  name: 'AppScore',
  props: ['score'],
  computed: {
    appScore() {
      return Math.round(this.score * 10);
    },
    scoreImage() {
      const imageName = 'rating/s' + this.appScore + '.svg';
      return imagePath('core', imageName);
    }
  }
};